import React, { PureComponent } from 'react'
import { Modal, Row, Col, Form, Button, message } from 'antd'
import { CsxFormSelect } from 'Form'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
import style from './style/Table.scss'
@connect(
  state => ({ ...state.abnormalLog, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    this.props.getErrorStatus()
  }
  handleSelectChange = () => {}
  handleSubmit = e => {
    const { form, modalData, handleTag } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = { ...values }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        let msg = '操作成功'
        console.log('reqObj is', reqObj)
        const params = {
          urlParams: [`${modalData && modalData.id}`],
          searchParams: { status: reqObj.status }
        }
        console.log('result is 999', params)
        handleTag({ ...params }, () => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { update } = this.props
    update({ modalData: { ...values } })
  }
  render() {
    const {
      modalTitle,
      form: { getFieldDecorator },
      modalTuttonLoading,
      errorStatusList,
      modalData
    } = this.props
    const {
      id = ''
    } = modalData
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
           <div class= {style.modalHead}>
             <div class = 'ant-col ant-form-item-label ant-col-xs-24 ant-col-sm-8'>
              <label class={'ant-form-item-required '} title="地点编码">任务ID：</label>
             </div>
            <div className ={ ` ant-col ant-form-item-control-wrapper ant-col-xs-24 ant-col-sm-16 ${style.dataShow}`}>{id}</div>
           </div>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='标记状态' hasFeedback>
                {getFieldDecorator('status', {
                  initialValue: modalData.status,
                  rules: [
                    {
                      required: true,
                      message: '请选择!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    data={errorStatusList}
                    selectItem={{
                      key: 'value',
                      valueKey: ['text']
                    }}
                  />
                )}
              </FormItem>
            </Col>

          </Row>
          <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



