import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import {} from 'api'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.abnormalLog }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 标记
  handleTag = () => {
    const { selectedRowKeys, update } = this.props
    if (!selectedRowKeys || selectedRowKeys.length < 1) {
      return message.error('没有可以操作的选项')
    } else if (selectedRowKeys && selectedRowKeys.length > 1) {
      return message.error('只能逐条进行标记')
    } else {
      return update({
        isShowModal: true,
        modalTitle: '标记',
        modalData: { id: selectedRowKeys[0] }
      })
    }
  }
  // 重推
  handleReSent = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleReSent,
      selectedRowKeys,
      update
    } = this.props
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      confirm({
        title: '温馨提示',
        content: '是否进行重推操作？',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleReSent(
            {
              data: selectedRowKeys
            },
            () => {
              update({ selectedRowKeys: [] })
              message.success('操作成功')
              handleGetList({ searchParams: { page, size, ...searchParams } })
            }
          )
        }
      })
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    const { menuPermissionIds = [], modalTuttonLoading } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('abnormalLog_reSend') && (
          <Button
            type='primary'
            loading={modalTuttonLoading}
            className={style.btn}
            onClick={this.handleReSent}
          >
            重推
          </Button>
        )}
        {menuPermissionIds && menuPermissionIds.includes('abnormalLog_tag') && (
          <Button type='primary' className={style.btn} onClick={this.handleTag}>
            标记
          </Button>
        )}
      </div>
    )
  }
}



