import React, { PureComponent } from 'react'
import { Table, Modal, message, Tooltip } from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
@connect(
  state => ({ ...state.abnormalLog, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '任务ID',
        width: 120,
        dataIndex: 'id',
        key: 'id'
      },
      {
        title: '接口编号',
        width: 80,
        dataIndex: 'orderType',
        key: 'orderType'
      },
      {
        title: '接口名称',
        width: 80,
        dataIndex: 'orderTypeName',
        key: 'orderTypeName'
      },
      {
        title: '状态',
        width: 80,
        dataIndex: 'status',
        key: 'status',
        render: (text, record, index) =>
          text == '0' ? '待处理' : text == '1' ? '处理中' : text == '2' ? '已处理' : ''
      },
      {
        title: '错误信息',
        width: 150,
        dataIndex: 'reason',
        key: 'reason',
        onCell: () => ({
          style: {
            maxWidth: 150,
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            cursor: 'pointer'
          }
        }),
        render: (text) => <Tooltip trigger='click' placement="topLeft" title={text}>{text}</Tooltip>
      },
      {
        title: '请求参数',
        width: 150,
        dataIndex: 'parameter',
        key: 'parameter',
        onCell: () => ({
          style: {
            maxWidth: 150,
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            cursor: 'pointer'
          }
        }),
        render: (text) => <Tooltip trigger='click' placement="topLeft" title={text}>{text}</Tooltip>
      },
      {
        title: '创建时间',
        width: 120,
        dataIndex: 'createTime',
        key: 'createTime'
      },
      {
        title: '执行成功时间',
        width: 120,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }
    ]
  }
  handleOperator(text, record, index, type) {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator
    } = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该储位？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: {
              id: record.id
            },
            urlParams: [record.id]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



