import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_GET_ERROR_STATUS_CODEANDNAME,
  API_GET_ERROR_TYPE_CODEANDNAME
 } from 'api'
import moment from 'moment'
const { RangePicker } = DatePicker
@connect(
  state => ({ ...state.abnormalLog }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {} })
  }
  render() {
    const { searchParams = {}, menuPermissionIds = [] } = this.props
    const {
      orderType,
      status,
      id,
      initTime = ''
     } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>任务ID：</label>
              <div className='box '>
                <Input
                  value={id}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ id: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_GET_ERROR_TYPE_CODEANDNAME}
              label='接口名称'
              allowClear={true}
              selectItem={{
                data: orderType,
                key: 'value',
                valueKey: ['text']
              }}
              onChangeSelect={data => {
                console.log('data selected is', data)
                this.onInputChange({ orderType: data })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_GET_ERROR_STATUS_CODEANDNAME}
              label='状态'
              allowClear={true}
              selectItem={{
                data: status,
                key: 'value',
                valueKey: ['text']
              }}
              onChangeSelect={data => {
                console.log('data selected is', data)
                this.onInputChange({ status: data })
              }}
            />
          </Col>
          <Col span={8}>
          <div className={'commonInput isTimePicker showTimePicker width_320'}>
              <label>创建日期：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  showTime
                  mode={['date', 'date']}
                  value={initTime}
                  onChange={(time, string) => {
                    let begin =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD HH:mm:ss')}`
                    let end =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD HH:mm:ss')}`
                    this.onInputChange({
                      begin,
                      end,
                      initTime: time
                    })
                  }}
                  format='YYYY-MM-DD HH:mm:ss'
                />
              </div>
            </div>
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('abnormalLog_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                  >
                    查询
                  </Button>
                )}
              {menuPermissionIds &&
                menuPermissionIds.includes('abnormalLog_reset') && (
                  <Button type='primary' onClick={this.reset}>
                    重置
                  </Button>
                )}
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



