import * as actionType from './actionType'
import { getData, delINArray } from 'util'
import {
  API_ABNORMALLOGS_PAGE,
  API_ABNORMALLOGS_RESENT,
  API_GET_ERROR_STATUS_CODEANDNAME,
  API_ABNORMALLOGS_TAG
} from 'api'
//同步
export const update = object => ({
  type: actionType.ABNORMALLOGS_UPDATE,
  data: object
})

// 获取 - 异常日志列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.ABNORMALLOGS_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_ABNORMALLOGS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.ABNORMALLOGS_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.ABNORMALLOGS_FAIL
      })
    })
}
// 重推 - 异常日志数据
export const handleReSent = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.ABNORMALLOGS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_ABNORMALLOGS_RESENT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.ABNORMALLOGS_UPDATE,
        data: {
          modalTuttonLoading: false
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.ABNORMALLOGS_FAIL
      })
    })
}
// 获得错误状态 - 异常日志数据
export const getErrorStatus = (reqData, callBack) => dispatch => {
  getData({ api: API_GET_ERROR_STATUS_CODEANDNAME, ...reqData })
    .then(data => {
      const { result = {} } = data
      const listFormatter = delINArray(['1'], result, 'value')
      callBack && callBack()
      dispatch({
        type: actionType.ABNORMALLOGS_UPDATE,
        data: {
          errorStatusList: listFormatter
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.ABNORMALLOGS_FAIL
      })
    })
}

// 标记 - 异常日志数据
export const handleTag = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.ABNORMALLOGS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_ABNORMALLOGS_TAG, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.ABNORMALLOGS_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.ABNORMALLOGS_FAIL
      })
    })
}



